import { Component, HostListener } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';

import { Project } from './shared/model/project.model';

import * as $ from 'jquery';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent {

	constructor(private router: Router, private route: ActivatedRoute) {

		$(function () { // wait for document ready

				$(window).scroll(() => {
					var y = $(window).scrollTop();
					if (y > 0) {
						$("#top-shadow").css({ 'display': 'block', 'opacity': y / 20 });
						$("#show_menu_btn").css({'color': 'white'});
					} else {
						$("#top-shadow").css({ 'display': 'block', 'opacity': y / 20 });
						$("#show_menu_btn").css({'color': 'black'});
					}
				});

		});

		this.router.events.subscribe(event => {
			if (event instanceof NavigationEnd) {
			  (<any>window).ga('set', 'page', event.urlAfterRedirects);
			  (<any>window).ga('send', 'pageview');
			}
		  });
	}

	
	changePage(name: string) {

		switch (name) {
			case "home":

				this.router.navigate([''], { relativeTo: this.route });
				break;

			case "about":

				this.router.navigate(['about/whoami'], { relativeTo: this.route });
				break;

			case "portfolio":

				this.router.navigate(['portfolio'], { relativeTo: this.route });
				break;

			case "personal":

				this.router.navigate(['portfolio/mywork'], { relativeTo: this.route });
				break;

			case "contact":

				this.router.navigate(['contact'], { relativeTo: this.route });
				break;

			default:
				break;
		}
	}

}
