import { Component, OnInit, Output, EventEmitter, HostListener } from '@angular/core';

import * as $ from 'jquery';
import { MyDetails } from '../shared/model/my-details.model';
import { AppData } from '../shared/app.data';

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss']
})
export class MenuComponent implements OnInit {

	details: MyDetails;

	public isMenuShown: boolean = false;

	@Output("onPageSelected") selectPage: EventEmitter<string> = new EventEmitter();

	@HostListener('window:keyup', ['$event'])
	keyEvent(event: KeyboardEvent) {
		// On pressing escape key dismiss, contact / project screens
		if (event.keyCode === 27) {
			this.isMenuShown = false;
		}
	}

	constructor(private appData: AppData) {
		this.details = appData.myDetails;
	}

	ngOnInit() {
	}

	onLinkClicked(data: string) {
		this.selectPage.emit(data);
		this.toggleMenu();
	}

	toggleMenu() {
		this.isMenuShown = !this.isMenuShown;
	}

}
