export class Project {

	constructor(
		private _title: string,
		private _platform: string,
		private _description: string,
		private _externalLink: string,
		private _repositoryLink: string,
		private _techonologiesUsed: Array<string>,
		private _imagePaths: Array<string>
	) { }

	get title(): string {
		return this._title;
	}

	get platform(): string {
		return this._platform;
	}

	get description(): string {
		return this._description;
	}

	get externalLink(): string {
		return this._externalLink;
	}

	get repositoryLink(): string {
		return this._repositoryLink;
	}

	get technologiesUsed(): Array<string> {
		return this._techonologiesUsed;
	}

	get imagePaths(): Array<string> {
		return this._imagePaths;
	}

	toString(): string {
		return "Project { " +
			"title: " + this.title + ", " +
			"platform: " + this.platform + ", " +
			"technologiesUsed: [ " + this.technologiesUsed + " ], " +
			"description: " + this.description + ", " +
			"externalLink: " + this.externalLink + ", " +
			"imagePaths: [ " + this.imagePaths + " ] " +
			"}";
	}

}