import { Component, OnInit, EventEmitter, Output, ElementRef, ViewChild, ViewContainerRef } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { AppData } from '../shared/app.data';
import { MyDetails } from '../shared/model/my-details.model';
import { Utils } from '../shared/service/utils.service';

import * as $ from 'jquery';
import { Validators, FormControl, FormGroup } from '@angular/forms';

@Component({
	selector: 'app-contact',
	templateUrl: './contact.component.html',
	styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

	details: MyDetails;
	contactForm: FormGroup;

	skypeUrl;

	constructor(private appData: AppData, public utils: Utils, public snackBar: MatSnackBar) {
		this.details = appData.myDetails;
		this.skypeUrl = utils.sanitize("skype:" + this.details.skypeId + "?call");

		const self = this;

		$(document).ready(function () {
			const scriptURL =
				'https://script.google.com/macros/s/AKfycbxEDX4BTZ7cQlIt8OfqXuEgM1_Uvzes3V2Li984dj5_M0Z-l-YC/exec'
			const form = document.forms['request']

			form.addEventListener('submit', e => {
				e.preventDefault()

				// Send data only if the form is valid
				if (self.contactForm.valid) {
					fetch(scriptURL, {
						method: 'POST',
						body: new FormData(form)
					})
						.then(response => self.openSnackBar('Will reply you ASAP.'))
						.catch(error => self.openSnackBar('Some Error Occurred. Instead give a call directly.'))
				}

			})
		});

	}

	ngOnInit() {
		this.contactForm = new FormGroup({
			name: new FormControl('', [Validators.required]),
			email: new FormControl('', [Validators.required, Validators.email]),
			message: new FormControl('', [Validators.required])
		})
	}

	openSnackBar(data) {
		this.snackBar.open(data, '', {
			duration: 1500,
			verticalPosition: 'bottom',
			horizontalPosition: 'center'
		});
	}

	get name() {
		return this.contactForm.get('name');
	}

	get email() {
		return this.contactForm.get('email');
	}

	get message() {
		return this.contactForm.get('message');
	}

	evaluateName() {
		return this.name.hasError('required') ? 'Please enter your good name' : '';
	}

	evaluateEmail() {
		return this.email.hasError('required') ? 'Your email id will help me revert you back' :
			this.email.hasError('email') ? 'Not a valid email' : '';
	}

	evaluateMessage() {
		return this.message.hasError('required') ? 'I would appreciate a \'Hello\' from you' : '';
	}

	getMobileNo() {
		return (<any>this.details.mobileNo).country_code + (<any>this.details.mobileNo).number;
	}

}
