export class MyDetails {

	constructor(
		private _name: string,
		private _tag: string,
		private _mobileNo: {
			country_code: string,
			number: string;
		},
		private _whatsAppNo: {
			country_code: string,
			number: string;
		},
		private _skypeId: string,
		private _emailId: string,
		private _linkedInUrl: string,
		private _githubUrl: string,
		private _bitbucketUrl: string,
		private _codeForcesUrl: string,
		private _codeChefUrl: string,
		private _hackerRankUrl: string,
		private _hackerEarthUrl: string,
		private _freeCodeCampUrl: string
	) { }

	get name() {
		return this._name;
	}

	get tag() {
		return this._tag;
	}
	
	get mobileNo() {
		return this._mobileNo;
	}

	get whatsAppNo() {
		return this._whatsAppNo;
	}

	get skypeId() {
		return this._skypeId;
	}

	get emailId() {
		return this._emailId;
	}

	get linkedInUrl() {
		return this._linkedInUrl;
	}

	get githubUrl() {
		return this._githubUrl;
	}
	
	get bitbucketUrl() {
		return this._bitbucketUrl;
	}

	get codeForcesUrl() {
 		return this._codeForcesUrl;
	}
	
	get codeChefUrl() {
		return this._codeChefUrl;
	}

	get hackerRankUrl() {
		return this._hackerEarthUrl;
	}

	get hackerEarthUrl() {
		return this._hackerEarthUrl;
	}
	
	get freeCodeCampUrl() {
		return this._freeCodeCampUrl;
	}

	toString(): string {
		return "MyDetails { " +
			"name: " + this.name + ", " +
			"tag: " + this.tag + ", " +
			"mobileNo: " + this.mobileNo + ", " +
			"whatsappNo: " + this.whatsAppNo + ", " +
			"emailId: " + this.emailId + ", " +
			"linkedInUrl: " + this.linkedInUrl + ", " +
			"githubUrl: " + this.githubUrl + ", " +
			"bitBucketUrl: " + this.bitbucketUrl + ", " +
			"codeForcesUrl: " + this.codeForcesUrl + ", " +
			"codeChefUrl: " + this.codeChefUrl + ", " +
			"hackerRankUrl: " + this.hackerRankUrl + ", " +
			"hackerEarthUrl: " + this.hackerEarthUrl + ", " +
			"freeCodeCampUrl: " + this.freeCodeCampUrl +
			" }"
	}

}