import { Component, OnInit, HostListener } from '@angular/core';
import { AppData } from '../shared/app.data';
import { Project } from '../shared/model/project.model';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
	selector: 'app-portfolio',
	templateUrl: './portfolio.component.html',
	styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit {

	showCaseData: Array<Project>;

	constructor(private route: ActivatedRoute, private appData: AppData) {
		if (route.snapshot.url.length == 0) {
			this.showPortfolioProjects();
		} else {
			this.showPersonalProjects();
		}
	}

	ngOnInit() {
	}

	ngOnDestroy() {
	}

	showPortfolioProjects() {
		this.showCaseData = this.appData.portfolioProjects;
	}

	showPersonalProjects() {
		this.showCaseData = this.appData.personalProjects;
	}

}
